.WatchListCard{
    width: 100%;
    margin: 15px auto;
    padding: 5px;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color:#4a626d;
    cursor: pointer;
    -webkit-box-shadow: 0 0 4px 2px rgba(0,0,0,.8);
            box-shadow: 0 0 4px 2px rgba(0,0,0,.8);
}

.WatchListCard:hover{
   background-color: black;
    -webkit-box-shadow: 0 2px 2px 4px rgba(0,0,0,.8);
            box-shadow: 0 2px 2px 4px rgba(0,0,0,.8);
    color: white;
}

.WatchListCard__poster{
    display: block;
    margin-right: 5px;
    height: 100px;
    -o-object-fit: cover;
       object-fit: cover;
    -ms-flex: 2 1;
        flex: 2 1;
}

.WatchListCard__body{
    height: 100px;
    -ms-flex: 3 1;
        flex: 3 1;
}

.WatchListCard__title{
    font-weight: bold;
    line-height: 1.4;
    font-size: 16px;
}

.WatchListCard__release, .WatchListCard__rating{
    font-size: 14px;
    margin-top: 8px;
}

.WatchListCard__btn{
    position: absolute;
    bottom: 5px;
    right: 5px;
    border-radius: 2px;
}

.WatchListCard__btn:hover{
    -webkit-transition: background-color 0.25s linear;
    -o-transition: background-color 0.25s linear;
    transition: background-color 0.25s linear;
    background-color: #62efff;
}


