.Logo{
    height: 70px;
    padding-right: 25px;
    -webkit-animation: Logo-rotate infinite 20s linear;
            animation: Logo-rotate infinite 20s linear;
}

@-webkit-keyframes Logo-rotate {
    0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
    25% { -webkit-transform: rotateY(-45deg); transform: rotateY(-45deg); }
    50% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
    75% { -webkit-transform: rotateY(45deg); transform: rotateY(45deg); }
    100% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
}

@keyframes Logo-rotate {
    0% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
    25% { -webkit-transform: rotateY(-45deg); transform: rotateY(-45deg); }
    50% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
    75% { -webkit-transform: rotateY(45deg); transform: rotateY(45deg); }
    100% { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
}


